import { Row, Col, Card, Button, Dropdown, ButtonGroup } from "react-bootstrap";
import MetarialDate from "../../components/Forms/Pickers/MetarialDate";
const Cards = () => {
	return(
		<>
        <div className="col-xl-12 col-lg-12">
            <div className="card bg-iadmin">
                <div className="card-body">
                    <div className="basic-form">
                    <form onSubmit={(e) => e.preventDefault()}>
                        <div className="row">
                            <div className="input-group mb-3 col-md-4 text-center" style={{ width: "30%", height: "56px" }}>
                    
                                <select
                                defaultValue={"option"}
                                className="form-control wide"
                                id="cabang"
                                >
                                    <option value="option" disabled>
                                        Cabang
                                    </option>
                                    <option value="1">Cabang</option>
                                    <option value="2">Cabang</option>
                                    <option value="3">Cabang</option>
                                </select>
                                {/* <span className="btn btn-primary" for="cabang">
                                    ▼
                                </span> */}
                            </div>
                            <div className="form-group mb-3 col-md-4">
                                {/* <MetarialDate /> */}
                                <input
                                type="text"
                                className="form-control input-default "
                                placeholder="Tanggal Awal"
                                />
                            </div>
                            <div className="form-group mb-3 col-md-4">
                                {/* <MetarialDate /> */}
                                <input
                                type="text"
                                className="form-control input-default "
                                placeholder="Tanggal Akhir"
                                />
                            </div>
                        </div>
                        <div className="row text-center">
                            <div className="col">
                                <Button className="me-2" variant="primary btn">
                                    <span className="text-dark">
                                        <i className="fa fa-search text-white" />
                                    </span>
                                    FILTER
                                </Button>
                                <Button className="me-2" variant="danger btn">
                                    
                                    RESET
                                </Button>
                            </div>
                        </div>
                    </form>
                    </div>
                </div>
            </div>
        </div>
        </>
)
} 
export default Cards;